import { Component, ElementRef, QueryList, ViewChild, ViewChildren } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator, MatPaginatorIntl, PageEvent } from '@angular/material/paginator';
import { MatRow, MatTableDataSource } from '@angular/material/table';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { APP_ROUTE } from '@core/constants';
import { AuthService } from '@core/data-access/services/auth.service';
import { BooleanStatus } from '@core/enums';
import { AgentStatus } from '@core/enums/agent.enum';
import { AgentPageMetaData, Agents, PageMetaData } from '@core/models';
import { ViewportService } from '@core/services/viewport.service';
import { AgentHubFacade } from '@feature/agent-hub/agent-hub.facade';
import { EDIT_DIALOG_STATIC_DATA } from '@feature/my-profile/my-profile.constant';
import { MyProfileFacade } from '@feature/my-profile/my-profile.facade';
import { AddAgentsDialogComponent } from '@feature/my-profile/v2/components/add-agents-dialog/add-agents-dialog.component';
import { BusinessInfoDialogComponent } from '@feature/my-profile/v2/components/business-info-dialog/business-info-dialog.component';
import { CannotRemoveAgentDialogComponent } from '@feature/my-profile/v2/components/cannot-remove-agent-dialog/cannot-remove-agent-dialog.component';
import { EditProfileInfoDialogComponent } from '@feature/my-profile/v2/components/edit-profile-info-dialog/edit-profile-info-dialog.component';
import { LeaveAgencyDialogComponent } from '@feature/my-profile/v2/components/leave-agency-dialog/leave-agency-dialog.component';
import { RemoveAgentDialogComponent } from '@feature/my-profile/v2/components/remove-agent-dialog/remove-agent-dialog.component';
import { DestroyService } from '@shared/services/destroy.service';
import { debounceTime, distinctUntilChanged, map, Observable, Subject, switchMap, takeUntil, tap } from 'rxjs';

class MyCustomPaginatorIntl extends MatPaginatorIntl {
	override getRangeLabel = (page: number, pageSize: number, length: number) => {
		if (length === 0 || pageSize === 0) {
			return `Page 1 of ${length}`;
		}

		length = Math.max(length, 0);
		return `Page ${page + 1} of ${Math.ceil(length / pageSize)}`;
	};
}

@Component({
    selector: 'findqo-agents',
    templateUrl: './agents.component.html',
    styleUrl: './agents.component.scss',
    providers: [DestroyService, { provide: MatPaginatorIntl, useValue: new MyCustomPaginatorIntl() }],
    standalone: false
})
export class AgentsComponent {
	constructor(
		private myProfileFacade: MyProfileFacade,
		public viewport: ViewportService,
		private dialog: MatDialog,
		private agentHubFacade: AgentHubFacade,
		private destory: DestroyService,
		private route: ActivatedRoute,
		private router: Router,
		protected readonly auth: AuthService
	) {
		this.usersMetaData$ = this.agentHubFacade.pageMetaData$;
		this.agentsList$ = this.myProfileFacade.getAgents$();
	}

	agentsList$: Observable<Agents[]>;

	@ViewChild(MatPaginator, { static: true }) paginator!: MatPaginator;
	@ViewChildren(MatRow, { read: ElementRef }) rows!: QueryList<ElementRef<HTMLTableRowElement>>;

	readonly pageSizeOptions = [25, 50, 100, 250, 500];
	readonly agentStatus = AgentStatus;
	readonly usersMetaData$: Observable<AgentPageMetaData>;

	readonly isAgent = this.auth.user()?.roles.some((role) => role === 'isAgentUser');
	readonly isAdmin = +this.auth.user()?.hasBusinessProfile && !this.isAgent;

	dataSource = new MatTableDataSource<Agents>();
	selectedRowId = '';
	pageLabel = '';
	pageMetadata!: AgentPageMetaData;
	isLoading = false;
	queryParams!: AgentPageMetaData;
	searchValue = '';
	searchSubject: Subject<string> = new Subject<string>();
	isCardView = true;
	displayedColumns: string[] = ['name', 'assignedAds', 'email', 'phone', 'status', 'dateAdded', 'action'];
	selectedItem;
	isRemoveAgentClicked = false;

	reInviteAgent(email: string): void {
		this.myProfileFacade.addAgents([email]);
	}

	viewAssignedAds(agent: Agents): void {
		this.router.navigate([`/agent/${agent.id}/${agent.profileId}`]);
	}

	editPhoneNumber(agent: Agents): void {
		if (agent.isAdmin && this.auth.user()?.id === agent?.userId) {
			this.router.navigate([APP_ROUTE.myProfile]);
			return;
		}

		const DATA = {
			...EDIT_DIALOG_STATIC_DATA.phone,
			...agent,
			target: 'phone',
			hint: 'Updating the phone number will also update the contact number throughout all ads assigned to this agent.',
		};

		this.dialog
			.open(EditProfileInfoDialogComponent, {
				disableClose: false,
				maxWidth: '375px',
				width: '95%',
				height: 'auto',
				maxHeight: '100%',
				data: DATA,
			})
			.afterClosed()
			.subscribe((value) => {
				if (value && value !== DATA?.['phone']) {
					this.myProfileFacade
						.patchAgentPhone(agent.id, {
							phone: value,
						})
						.subscribe(() => this.myProfileFacade.loadAgents());
				}
			});
	}

	removeAgentRequest(agent: Agents, configData): void {
		this.dialog
			.open(RemoveAgentDialogComponent, {
				data: agent,
				...configData,
			})
			.afterClosed()
			.subscribe(() => (this.isRemoveAgentClicked = false));
	}

	leaveAgency(agent: Agents, configData): void {
		this.dialog
			.open(LeaveAgencyDialogComponent, {
				data: agent,
				...configData,
			})
			.afterClosed()
			.subscribe(() => (this.isRemoveAgentClicked = false));
	}

	removeAgent(agent: Agents): void {
		if (!this.isRemoveAgentClicked) {
			this.isRemoveAgentClicked = true;
			const IS_AGENT_LEAVING = agent.userId === this.auth.user()?.id;

			const CONFIG_DATA = {
				width: '95%',
				height: 'auto',
				maxHeight: 'unset',
				maxWidth: IS_AGENT_LEAVING ? '600px' : '375px',
				panelClass: 'remove-agent',
				disableClose: true,
				data: { ...agent, businessProfile: this.auth.user()?.businessProfile },
			};

			// Do not check assigned Ad if request is pending or rejected
			if (agent.status !== this.agentStatus.ACCEPTED) {
				if (IS_AGENT_LEAVING) {
					this.leaveAgency(agent, CONFIG_DATA);
				} else {
					this.removeAgentRequest(agent, CONFIG_DATA);
				}
				return;
			}

			//check ig agent has any assigned ads
			this.myProfileFacade
				.canRemoveAgent(agent?.id)
				.then((response: { hasAssignedAd: string }) => {
					this.isRemoveAgentClicked = false;

					if (response?.hasAssignedAd === BooleanStatus.FALSE) {
						if (IS_AGENT_LEAVING) {
							this.leaveAgency(agent, CONFIG_DATA);
						} else {
							this.removeAgentRequest(agent, CONFIG_DATA);
						}
					} else {
						if (IS_AGENT_LEAVING) {
							this.dialog.open(CannotRemoveAgentDialogComponent, {
								...CONFIG_DATA,
								data: { ...CONFIG_DATA.data, isAgentLeaving: IS_AGENT_LEAVING },
							});
						} else {
							this.dialog.open(CannotRemoveAgentDialogComponent, CONFIG_DATA);
						}
					}
				})
				.catch(() => {
					this.isRemoveAgentClicked = false;
				});
		}
	}

	onClearSearch(): void {
		this.searchValue = '';
		this.router.navigate([], {
			relativeTo: this.route,
			queryParams: { search: null },
			queryParamsHandling: 'merge',
		});
	}

	addNewAgents(): void {
		const USER = this.myProfileFacade.getUser();

		const CONFIG_DATA = {
			width: '100%',
			maxWidth: '500px',
			height: 'auto',
			disableClose: true,
			maxHeight: 'unset',
		};

		if (+USER.hasBusinessProfile) {
			this.dialog.open(AddAgentsDialogComponent, { ...CONFIG_DATA, panelClass: 'agent-dialog' });
		} else {
			this.dialog.open(BusinessInfoDialogComponent, { panelClass: 'business-info', ...CONFIG_DATA });
		}
	}

	onSearch(keyword: string): void {
		keyword = keyword.trim();

		this.searchSubject.next(keyword);
		this.searchValue = keyword;
	}

	getStatusClass(status: string, isAdmin?: string): string {
		if (isAdmin === '1') status = 'admin';

		switch (status) {
			case '1':
				return 'badge-pending';
			case '2':
				return 'badge-accepted';
			case '3':
				return 'badge-rejected';
			case 'admin':
				return 'badge-admin';
			default:
				return '';
		}
	}

	onPageChange(paginator: PageEvent, metaData: AgentPageMetaData | null): void {
		const currentQueryParams = this.route.snapshot.queryParams;

		this.router.navigate([APP_ROUTE.agentHub.dashboard, APP_ROUTE.agentHub.agents], {
			queryParams: {
				...currentQueryParams,
				page: paginator.pageIndex + 1,
				perPage: paginator.pageSize,
			},
			queryParamsHandling: 'merge',
		});
	}

	scrollToIndex(id: string): void {
		setTimeout(() => {
			this.selectedRowId = id;

			const ELEMENT = this.rows.find((row) => row.nativeElement.id === id);

			if (ELEMENT) {
				ELEMENT.nativeElement.scrollIntoView({
					block: 'center',
					behavior: 'auto',
				});
			}
		});
	}

	updatePaginator(metaData: PageMetaData): void {
		const startIndex = (Number.parseInt(metaData?.currentPage) - 1) * Number.parseInt(metaData?.perPage) + 1;
		const endIndex = Math.min(startIndex + Number.parseInt(metaData?.perPage) - 1, Number.parseInt(metaData?.total));
		this.pageLabel = `Showing ${startIndex} to ${endIndex} of ${metaData?.total} item(s)`;
	}

	ngOnInit(): void {
		this.agentHubFacade.pageMetaData$.subscribe((metaData: PageMetaData) => {
			this.pageMetadata = metaData;
			this.updatePaginator(metaData);
		});

		this.searchSubject
			.pipe(
				debounceTime(1000),
				distinctUntilChanged(),
				tap((search) => {
					if (search?.length === 0) search = null;
					const currentQueryParams = this.route.snapshot.queryParams;

					this.router.navigate([APP_ROUTE.agentHub.dashboard, APP_ROUTE.agentHub.agents], {
						queryParams: {
							...currentQueryParams,
							page: 1,
							perPage: this.pageMetadata.perPage,
							search,
						},
						queryParamsHandling: 'merge',
					});
				}),
				takeUntil(this.destory)
			)
			.subscribe();
	}

	ngAfterViewInit(): void {
		this.dataSource.paginator = this.paginator;

		this.route.queryParams.subscribe((response: any) => {
			this.myProfileFacade.loadUser(response);

			if (response?.search) {
				this.searchValue = response?.search ?? '';
			}
		});
	}
}
